<template>
  <div class="err">
    <el-row>
      <el-col :span='24' class='list2'>
        <el-form inline class='form'>
          <el-form-item label=''>
            <el-select v-model="form.grade" size='mini' placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
            <el-select v-model="form.clazz" size='mini' placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=''>
            <el-select v-model="form.tool" size='mini' placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type='info' size='mini' @click="more1"></el-button>
            <el-button type='info' size='mini' @click="more2"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-table :data="binds" style="width:100%;" class='table2' height='400'>
      <el-table-column type='index' label="" width="180"></el-table-column>
      <el-table-column prop="no" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="gender" label="">
      </el-table-column>
      <el-table-column prop="bindNo" label=""></el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
  name: 'err',
  data(){
    return {
      form:{},
      binds:[{
        no: '20170101',
        name: '',
        gender:'',
        bindNo:'SH-1001',
      }, {
        no: '20170102',
        name: '',
        gender:'',
        bindNo:'SH-1001',
      }, {
        no: '20170103',
        name: '',
        gender:'',
        bindNo:'SH-1001',
      }],
    }
  },
  methods:{
  	more1() {
      this.$message({
          title: '',
          message: '',
          type: 'success'
        });
      },
      more2() {
        this.$message({
          title: '',
          message: '',
          type: 'success'
        });
      },
    rc2({row,column,rowIndex,columnIndex}){//
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1,2];
        } else if (columnIndex === 1) {
          return [0,0];
        }
      }
    },
    rc({row,column,rowIndex,columnIndex}){
      if (columnIndex===0||columnIndex===1||columnIndex===2){  //1230
        if (rowIndex%2===0) {//
          return [2,1];
        } else {
          return [0,0];
        }
      }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .font{
    display:inline-block;
    line-height:30px;
  }
  .td{
    height:80px;
    border:1px solid black;
    margin-left:10px;
    text-align:center;
    padding:10px;
    margin:5px;
  }
  .content{
    border:1px solid #999;
    box-shadow:6px 6px 12px #999;
    padding:10px 0;
    height:480px;
    overflow:auto;
  }
  .errStu{
    height:230px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
  }
  .getInfo{
    height:200px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
  }
  .query:hover{
    text-decoration:underline;
    color:red;
    cursor:pointer;
  }
  .title{
    margin-top:20px;
    font-size:20px;
  }
  .table2{
    overflow:auto;
  }
  .list2 .el-select{
    width:142px;
  }
</style>



