<template>
	<div class="data">
		<el-row>
      <el-col :span='24'>
      	<el-form :inline="true" :data="form" class="demo-form-inline" size='mini'>
          <el-form-item label="">
            <el-date-picker size='mini' v-model="form.time" type="date" placeholder=""></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.grade" placeholder="" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.clazz" placeholder="" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.course" placeholder=''> 
              <el-option label=" 1600-16:45" value=" 1600-16:45"></el-option>
              <el-option label=" 1000-10:20" value=" 1000-10:20"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type='info' size='mini'></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
    	<el-col :span='24'>
    		<el-row type='flex' justify='center'><span class='title'></span></el-row>
    	</el-col>
			<el-table :data="classData" style="width:100%;" height='430' class='table2' size='mini'>
		    <el-table-column fixed prop="class" label="" width="80">
		    </el-table-column>
		    <el-table-column prop="date" label="" width="120">
		    </el-table-column>
		    <el-table-column prop="teacher" label="" width="100">
		    </el-table-column>
		    <el-table-column prop="students" label="" width="80">
		    </el-table-column>
		    <el-table-column prop="lowRate" label="" width="100">
		    </el-table-column>
		    <el-table-column prop="averageRate" label="" width="100">
		    </el-table-column>
		    <el-table-column prop="heightRate" label="" width="100">
		    </el-table-column>
		    <el-table-column prop="closeLowStu" label="" width="120">
		    </el-table-column>
		    <el-table-column prop="closeHeightStu" label="" width="120">
		    </el-table-column>
		    <el-table-column prop="overLowRate" label="" width="120">
		    </el-table-column>
		    <el-table-column prop="overHeightRate" label="" width="120">
		    </el-table-column>
		    <el-table-column prop="lesson" label="" width="80">
		    </el-table-column>
		  </el-table>
	  </el-row>
	</div>
</template>

<script type="text/javascript">
export default {
  name: 'data',
  data () {
    return {
    	form:{},
      classData: [{
	      class:'1',
	      date: '2017120114:30~15:15',
	      teacher: '',
	      students: '45',
	      lowRate: '50',
	      averageRate:'79',
	      heightRate: '102',
	      closeLowStu: '2',
	      closeHeightStu:'1',
	      overLowRate:'1',
	      overHeightRate:'1',
	      lesson:''
	    },{
	      class:'2',
	      date: '2017120114:30~15:15',
	      teacher: '',
	      students: '45',
	      lowRate: '50',
	      averageRate:'79',
	      heightRate: '102',
	      closeLowStu: '2',
	      closeHeightStu:'1',
	      overLowRate:'1',
	      overHeightRate:'1',
	      lesson:''
	    }],
    }
  },
}
</script>

<style scoped>
	*{
		font-size:12px;
	}
	.title{
		font-size:20px;
		margin:20px auto;
	}
	.table2{
		overflow:auto;
	}

</style>



