<!--   -->
<template>
	<div class="teacher">
		<el-row>
			<el-col :span='16'>
				<el-form inline>
					<el-form-item label=''>
						<el-input v-model='keys' size='mini'></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type='info' size='mini'></el-button>
					</el-form-item>
				</el-form>
			</el-col>
			<el-col :span='8'>
				<el-row type='flex' justify='end'>
          <el-form inline>
            <el-form-item>
    					<el-button type='info' size='mini' @click='single2'></el-button>
    		      <el-button type='info' size='mini' @click='admin2'></el-button>
    		      <el-button type='info' size='mini' @click='batch2'></el-button>
            </el-form-item>
          </el-form>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="teacher" style="width: 100%" height="426" class='table'>
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="name" label="" width="150">
      </el-table-column>
      <el-table-column prop="user" label="" width="150">
      </el-table-column>
      <el-table-column prop="passwd" label="" width="150">
      </el-table-column>
      <el-table-column prop="phone" label="" width="150">
      </el-table-column>
      <el-table-column prop="gender" label="" width="150">
      </el-table-column>
      <el-table-column prop="state" label="" width="120">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.user" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.passwd" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-input v-model="form2.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form2.gender" label=""></el-radio>
          <el-radio class="radio" v-model="form2.gender" label=""></el-radio>
        </el-form-item>
        <el-form-item label="" :label-width="fw">
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
          <el-radio class="radio" v-model="form2.state" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2"> </el-button>
        <el-button type="info" @click="submit2"> </el-button>
      </div>
    </el-dialog>

    <el-dialog :title="titleAdmin" :visible.sync="visibleAdmin" center>
      <el-form :model="formAdmin">
        <a></a><el-checkbox v-model="formAdmin.survey"></el-checkbox><hr color='#eee'>
        <a></a><el-checkbox v-model="formAdmin.data"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.curve"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.clazzData"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.err"></el-checkbox><hr color='#eee'>
        <a></a><el-checkbox v-model="formAdmin.set"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.unit"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.user"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.table"></el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.rate"></el-checkbox><hr color='#eee'>
      </el-form>
      <div slot="footer" class="dialog-footer">
         <el-button @click="cancleAdmin"> </el-button>
         <el-button type="info" @click="submitAdmin"> </el-button>
      </div>
    </el-dialog>  
	</div>
</template>

<script>
var flag = 0;
export default {
  name: 'real',
  data(){
    return {
      fw:'120px',
    	keys:'',
      title2:'',
      titleAdmin:'',
      form2:{},
      formAdmin:{},
      visible2:false,
      visibleAdmin:false,
      teacher:[{
        no:'1',
        name:'larry',
        user:'123',
        passwd:'123',
        phone:'176',
        gender:'',
        state:'1',
      },{
        no:'2',
        name:'terry',
        user:'123',
        passwd:'123',
        phone:'176',
        gender:'',
        state:'1',
      }],
    }
  },
  methods:{
    edit2(i,row){this.visible2=true;flag=0;this.title2='';},
    delete2(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    single2(){this.visible2=true;flag=0;this.title2='';},
    batch2(){this.visible2=true;flag=1;this.title2='';},
    admin2(){this.visibleAdmin=true;this.titleAdmin='';},
    submit2(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
      flag==1 ? single2() : this.visible2=false;},
    submitAdmin(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
      this.visibleAdmin=false;},
    cancle2(){this.visible2=false;flag=0;},
    cancleAdmin(){this.visibleAdmin=false;},
  }
}
</script>

<style scoped>
  .icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog-footer{
    text-align:right;
  }
  a{
    display:inline-block;
    width:50px;
  }
  b{
    display:inline-block;
    width:100px;
  }
</style>


