<!--  -->
<template>
	<div class="student">
		<el-row>
			<el-col :span='18'>
				<el-form inline :model='stu'>
					<el-form-item label=''>
				    <el-select v-model="stu.grade" placeholder="" size='mini'>
				      <el-option label="" value=""></el-option>
				      <el-option label="" value=""></el-option>
				      <el-option label="" value=""></el-option>
				      <el-option label="" value=""></el-option>
				      <el-option label="" value=""></el-option>
				    </el-select>
					</el-form-item>
					<el-form-item label=''>
						<el-select v-model="stu.clazz" placeholder="" size='mini'>
				      <el-option label="" value=""></el-option>
				      <el-option label="" value=""></el-option>
				      <el-option label="" value=""></el-option>
				    </el-select>
					</el-form-item>
					<el-form-item label=''>
						<el-input v-model='stu.keys' size='mini'></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type='info' size='mini'></el-button>
					</el-form-item>
				</el-form>
			</el-col>
			<el-col :span='6'>
				<el-row type='flex' justify='end'>
          <el-form inline>
            <el-form-item>
              <el-button type='info' size='mini' @click='single3'></el-button>
              <el-button type='info' size='mini' @click='batch3'></el-button>
            </el-form-item>
          </el-form>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="student" style="width: 100%" height="426" class='table'>
      <el-table-column prop="no" label="" width="100">
      </el-table-column>
      <el-table-column prop="user" label="" width="100">
      </el-table-column>
      <el-table-column prop="name" label="" width="100">
      </el-table-column>
      <el-table-column prop="stuNo" label="" width="100">
      </el-table-column>
      <el-table-column prop="gender" label="" width="100">
      </el-table-column>
      <el-table-column prop="num" label="" width="100">
      </el-table-column>      
      <el-table-column prop="lowRate" label="" width="100">
      </el-table-column>
      <el-table-column prop="averageRate" label="" width="100">
      </el-table-column>
      <el-table-column prop="heightRate" label="" width="120">
      </el-table-column>
      <el-table-column prop="firstStage" label="" width="120">
      </el-table-column>
      <el-table-column prop="secondStage" label="" width="120">
      </el-table-column>
      <el-table-column prop="thirdStage" label="" width="120">
      </el-table-column>
      <el-table-column prop="tall" label="" width="100">
      </el-table-column>
      <el-table-column prop="weight" label="" width="100">
      </el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit3(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete3(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title3" :visible.sync="visible3" center>
      <el-form :model="form3" class='dialog' size='small'>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.num" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-select v-model="form3.clazz" placeholder="" style='width:210px;'>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.heightRate" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.no" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.lowRate" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.user" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.averageRate" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.name" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.firstStage" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.stuNo" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.secondStage" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.tall" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.thirdStage" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form3.weight" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-radio class="radio" v-model="form3.state" label=""></el-radio>
              <el-radio class="radio" v-model="form3.state" label=""></el-radio>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-radio class="radio" v-model="form3.gender" label=""></el-radio>
              <el-radio class="radio" v-model="form3.gender" label=""></el-radio>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3"> </el-button>
        <el-button type="info" @click="submit3"> </el-button>
      </div>
    </el-dialog> 
	</div>
</template>

<script>
var flag = 0;
export default {
  name: 'student',
  data(){
    return {
      fw:'100px',
    	stu:{},
      title3:'',
      form3:{},
      visible3:false,
      student:[{
        no:'1',
        user:'123',
        name:'larry',
        stuNo:'201701',
        gender:'',
        num:'SH089421',
        lowRate:'50',
        averageRate:'75',
        heightRate:'100',
        firstStage:'60',
        secondStage:'70',
        thirdStage:'80',
        tall:'165',
        weight:'120',
      },{
        no:'2',
        user:'124',
        name:'terry',
        stuNo:'201702',
        gender:'',
        num:'SH089422',
        lowRate:'50',
        averageRate:'75',
        heightRate:'100',
        firstStage:'60',
        secondStage:'70',
        thirdStage:'80',
        tall:'170',
        weight:'120',
      }]
    }
  },
  methods:{
    edit3(i,row){this.visible3=true;flag=0;this.title3='';},
    delete3(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    single3(){this.visible3=true;flag=0;this.title3='';},
    batch3(){this.visible3=true;flag=1;this.title3='';},
    submit3(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
      flag==1 ? single3() : this.visible3=false;},
    cancle3(){this.visible3=false;flag=0;},
  }
}
</script>

<style scoped>
  .icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  /* .dialog .el-input{
    width:430px;
  } */
  .dialog-footer{
    text-align:right;
  }
</style>


