<template>
	<!--<div class="unit" :data='unit'>-->
		<el-row class="unit" :data='unit'>
		  	<el-col :span="12" class="tab">
			  	<el-row>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.name'></div></el-col>
				</el-row>
				<el-row>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.address'></div></el-col>
				</el-row>
				<el-row>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.type'></div></el-col>		
				</el-row>
				<el-row>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.type'></div></el-col>
				</el-row>
				<el-row>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				  <el-col :span="16"><div class="grid-content bg-purple">
				  	<select v-model='unit.year'>
			          <option value="2017-2018">2017-2018</option>
			          <option value="2016-2017">2016-2017</option>
		        	</select></div>
				  </el-col>
				</el-row>
				<el-row>
				  <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
				  <el-col :span="16"><div class="grid-content bg-purple">
				  	<select v-model='unit.term'>
			          <option value=""></option>
			          <option value=""></option>
			        </select></div>
				  </el-col>
				</el-row>
				<el-button type='info' class='emit' @click='edit'></el-button>
				<!--<el-row class="tab">
				  <el-col :span="2" class="logo" offset="22"><el-button type='info' size='small' class='emit'></el-button></el-col>		  
				</el-row>-->
			</el-col>
			<el-col :span="12">
				<el-row class="logo">
					<el-col :span="12" :offset="6" class="logo"><div class="grid-content bg-purple-logo">logo</div></el-col>
				</el-row>
				<el-row class="logo">
					<el-col :span="10" :offset="6" class="logo"><el-input v-model="input" placeholder="" style='width:214px;'></el-input></el-col>
				  <el-col :span="2"><el-button type="primary" @click='up'><i class="el-icon-upload el-icon-right"></i></el-button></el-col>		  
				</el-row>
			</el-col>
		</el-row>
	<!--</div>-->
</template>

<script type="text/javascript">
export default {
  name: 'unitInfo',
  data(){
    return {
    	input: '',
      unit:{
        name : '',
        address : '1',
        phone : '021-123456789',
        type : '',
        year : '2016-2017',
        term : '',
      },
  	}
  },
  methods:{
    edit(){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    up(){
      this.$message({
        message: 'logologo',
        type: 'success',
      });
    },
  },
}
</script>

<style type="text/css" scoped>
	.unit{
    width:100%;
    height:450px;
    padding: 40px;
    margin-top: 20px;
    border:1px solid #CCCCCC;
  }
  
  .el-row {
    border-bottom:0px solid #CCCCCC; 
    border:1px solid #CCCCCC;       
  }
  .el-col {
    text-align: center;
  }
  .grid-content {
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
  }
  input{
  	width: 99%;
  	height: 28px;
  }
  select{
  	width: 100%;
  	height: 33px;
  }
  .bg-purple-logo{
    width:296px;
  	height: 200px;
  	line-height: 200px;
  	border:1px solid #CCCCCC;
  }
  .logo,.tab{
  	border:0px;
  }
  .emit{
  	margin-top: 50px;
  }
</style>


