<!--   -->
<template>
	<div class="clazzSetting">
		<el-row>
			<el-col :span='12'>
				<span></span>
				<el-select v-model="course" placeholder="" size='mini'>
		      <el-option label="" value=""></el-option>
		      <el-option label="" value=""></el-option>
		      <el-option label="" value=""></el-option>
		      <el-option label="" value=""></el-option>
		      <el-option label="" value=""></el-option>
		    </el-select>
			</el-col>
			<el-col :span='12'>
				<el-row type='flex' justify='end'>
					<el-button type='info' size='small' @click='add4'></el-button>
					<el-button type='info' size='small' @click='batch4'></el-button>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="clazzData" style="width: 100%" height="450" class='table'>
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="clazz" label="">
      </el-table-column>
      <el-table-column prop="clazzSingle" label="" width="80">
      </el-table-column>
      <el-table-column prop="teacher" label="">
      </el-table-column>
      <el-table-column prop="lowRate" label="">
      </el-table-column>
      <el-table-column prop="averageRate" label="">
      </el-table-column>
      <el-table-column prop="heightRate" label="">
      </el-table-column>
      <el-table-column prop="firstStage" label="">
      </el-table-column>
      <el-table-column prop="secondStage" label="">
      </el-table-column>
      <el-table-column prop="thirdStage" label="">
      </el-table-column>
       <el-table-column prop="state" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit4(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete4(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title4" :visible.sync="visible4" center>
      <el-form :model="form4" class='dialog' size='small'>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-select v-model="form4.teacher" placeholder="" style='width:214px;'>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-select v-model="form4.grade" placeholder="" style='width:214px;'>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.no" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.clazz" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.firstStage" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.clazzSingle" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.secondStage" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.averageRate" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.thirdStage" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.lowRate" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-input v-model="form4.heightRate" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="" :label-width="fw">
              <el-radio class="radio" v-model="form4.state" label=""></el-radio>
              <el-radio class="radio" v-model="form4.state" label=""></el-radio>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle4"> </el-button>
        <el-button type="info" @click="submit4"> </el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script type="text/javascript">
var flag = 0;
export default {
  name: 'clazzSetting',
  data(){
    return {
      fw:'100px',
      title4:'',
      course:'',
      form4:{},
      visible4:false,
      clazzData: [{
        clazz:'1',
        clazzSingle: '1',
        teacher: '',
        lowRate: '58/',
        averageRate:'75/',
        heightRate: '100/',
        firstStage: '75/',
        secondStage:'85/',
        thirdStage:'95/',
        state:'',
        opetation:''
      }, {
        clazz:'2',
        clazzSingle: '2',
        teacher: '',
        lowRate: '58/',
        averageRate:'75/',
        heightRate: '100/',
        firstStage: '75/',
        secondStage:'85/',
        thirdStage:'95/',
        state:'',
        opetation:''
      }],
    }
  },
  methods:{
    add4(){this.visible4=true;flag=0;this.title4='';},
    batch4(){this.add4();flag=1;this.title4='';},
    edit4(i,row){this.visible4=true;this.title4='';},
    delete4(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
    },
    submit4(i,row){
      this.$message({
        message: '',
        type: 'success',
      });
      flag==1?add4():this.visible4=false;},
    cancle4(){this.visible4=false;flag=0;},
  },
}
</script>

<style scoped>
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  .table{
    overflow:auto;
  }
  /* .dialog .el-input{
    width:430px;
  } */
  .dialog-footer{
    text-align:right;
  }
</style>


